*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  color: #47404C;
  width: 100%;
  min-height: 100vh;
  background-color: #ffffff;
  font-family: Gilroy, "Arial", sans-serif;
  line-height: 1.2; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

h1,
h2,
h3,
h4,
p,
figure,
fieldset,
ul {
  margin: 0; }

ul {
  padding-left: 0; }

li {
  list-style: none; }

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

fieldset {
  padding: 0;
  border: none; }

button:focus,
input:focus,
a:focus {
  outline: none; }

button {
  padding: 0;
  border: none; }

.size-1 {
  font-size: 1rem; }

.size-2 {
  font-size: 1.125rem; }

.container {
  max-width: 1220px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.hover-opacity {
  transition: 0.2s; }
  .hover-opacity:hover {
    opacity: 0.5; }

.tablet-visibility {
  display: none; }
  @media screen and (max-width: 768px) {
    .tablet-visibility {
      display: block; } }

@media screen and (max-width: 768px) {
  .tablet-hidden {
    display: none; } }

.phone-visibility {
  display: none; }
  @media screen and (max-width: 600px) {
    .phone-visibility {
      display: block; } }

@media screen and (max-width: 600px) {
  .phone-hidden {
    display: none; } }

.page-body {
  display: flex; }
  .page-body__inner {
    background-color: #F7F7FA;
    flex-grow: 1;
    display: flex;
    flex-direction: column; }

.page-header {
  width: 288px;
  background-color: #222124; }
  .page-header__logo {
    display: block;
    height: 80px;
    display: flex;
    align-items: center;
    padding-left: 28px;
    border-bottom: 1px solid #3F4253; }

.header-nav__link {
  color: #fff;
  display: block;
  border-bottom: 1px solid #3F4253;
  padding-left: 28px;
  padding-top: 13px;
  padding-bottom: 13px; }

.header-nav__link--active {
  position: relative;
  background-color: #19181B; }
  .header-nav__link--active:hover {
    opacity: 1; }
  .header-nav__link--active::before {
    content: "";
    position: absolute;
    width: 5px;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #C74B4E; }

.page-footer {
  margin-top: auto; }
  .page-footer__inner {
    display: flex;
    justify-content: space-between;
    margin-left: 64px;
    margin-right: 64px;
    border-top: 1px solid #E0E3EE;
    padding-top: 30px;
    padding-bottom: 30px; }
  .page-footer__copy {
    font-size: 14px;
    color: #060606;
    font-weight: 500; }
  .page-footer__portal {
    font-size: 14px;
    color: #060606;
    opacity: 0.5; }
  .page-footer__anonim-link {
    font-size: 14px;
    color: #060606;
    font-weight: 500; }
